.App {
  font-family: sans-serif;
  text-align: center;
  background-color: #313131;
}

.Background {
  width: 900;
  height: 600px;
  background-color: #313131;
  display: flex;
  font-size: 12px;
  align-items: center;
}

.Card {
  width: 300px;
  height: 580px;
  background-color: #f2f2f2;
  margin-left: 13%;
  background-image: url(bgresize.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.card-container {
  position: relative;
  /* max-width: 226px; */
  margin: 0 auto;
}

.global-mining-container {
  float: left;
}

/*global mining button*/
.global-mining-button {
  background-color: #aaa7a7;
  border: 2px solid #313131;
  opacity: 70%;
}

.global-mining-button:hover {
  background-color: #313131;
  border: 2px solid #313131;
}

.alert {
  position: absolute;
  height: 220px;
  width: 220px;
  background-color: #313131;
  border: 2px solid #aaa7a7;
  color: white;
  box-shadow: 0px -10px 30px rgb(110, 108, 108);
}

.close-button {
  background-color: #aaa7a7;
  border: 2px solid #313131;
  margin-left: 20px;
}

.close-button:hover {
  background-color: #313131;
  border: 2px solid #aaa7a7;
}

img {
  border-radius: 500%;
  border: 1px solid black;
  margin-top: 20px;
  margin-left: 15px;
  float: left;
  box-shadow: 0px -10px 30px rgb(110, 108, 108);
}

img:hover {
  border: 2px solid rgb(203, 203, 204);
}

.paragraph {
  font-size: 20px;
  border: 1px solid rgb(241, 235, 235);
  background: rgb(56, 49, 49);
  color: white;
  height: 200px;
  padding: 2%;
}

.pagination {
  margin-left: 40%;
}

/* appear - on page load */
.fade-appear {
  opacity: 0;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 1000ms linear;
}

/*on enter */
.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 1000ms linear;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 1000ms linear;
}

.fade-exit-done {
  opacity: 0;
}

/* slide enter */
.slide-enter {
  opacity: 0;
  transform: scale(0.97) translateY(5px);
  z-index: 1;
}
.slide-enter.slide-enter-active {
  opacity: 1;
  transform: scale(1) translateY(0);
  transition: opacity 3000ms linear 1000ms, transform 3000ms ease-in-out 1000ms;
}

/* slide exit */
.slide-exit {
  opacity: 1;
  transform: scale(1) translateY(0);
}
.slide-exit.slide-exit-active {
  opacity: 0;
  transform: scale(0.97) translateY(5px);
  transition: opacity 1500ms linear, transform 1500ms ease-out;
}
.slide-exit-done {
  opacity: 0;
}

button {
  background-color: #8382b5;
  color: #fff;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.page-link {
  background-color: #665490;
  color: white;
}

.page-item {
  padding: 0.5%;
}

.page-item:hover {
  color: black;
}

.table {
  width: 35%;
  margin-left: 494px;
  opacity: 55%;
  color: black;
  border: 1px solid black;
  box-shadow: 0px -10px 30px rgb(85, 84, 84);
}

td {
  text-align: left;
}

.alert-enter {
  opacity: 0;
  transform: scale(0.2);
}
.alert-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 200ms, transform 200ms;
}
.alert-exit {
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 200ms, transform 200ms;
}

.mining {
  font-size: 15px;
  margin-left: 20px;
}

.alert-heading {
  font-size: 18px;
  font-weight: bold;
  margin-left: 20px;
}
